import React from 'react';
import autobind from 'class-autobind';
import DataStore from '../datastore'
import Marks from './marks'
import './dashboard.css'

export default class DashBoard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {pc: null, open: null};
        autobind(this);
        this.getData();
    }

    async getData() {
        let pc = await DataStore.getMarks();
        this.setState({pc: pc.pledges});
        this.props.setName(pc.name)
    }

    openPledge(pledge) {
        this.setState({open: pledge});
    }

    onClose(pledge) {
        this.setState({open: null});
    }

    render() {
        let pc = this.state.pc;
        let open = this.state.open;

        if (!pc)
            return (
            <div className="dash">
                <div className="loader"/>
            </div>);

        let Modal = () => {
            if (open === null)
                return null;
            let marks = open.getMarks();
            let details = []
            marks.forEach(m => {
                details.push(
                <tr key={m.desc} className="details">
                    <td><Marks type={m.type} count={m.count}/></td>
                    <td className="details">{m.brother}</td>
                    <td className="details">{m.desc}</td>
                </tr>)
            });
            
            return([
                <div id="modal">
                    <h3>{open.name}</h3>
                    <table>
                        <tbody>
                            <tr>
                                <th width="10%">
                                    marks
                                </th>
                                <th width="20%">
                                    brother
                                </th>
                                <th width="30%">
                                    description
                                </th>
                            </tr>
                            {details}
                        </tbody>
                    </table>
                    <br></br>
                </div>,
            <div onClick={this.onClose} id="fade">
                
            </div>
            ]);
        }

        let Pledge = (pledge) => {
            return(
                <tr key={pledge.name} onClick={() => this.openPledge(pledge)}>
                    <td>{pledge.name}</td>
                    <td><Marks type="black" count={pledge.getTotal("black")}/></td>
                    <td><Marks type="white" count={pledge.getTotal("white")}/></td>
                </tr>)
        }

        let pledges = [];
        pc.forEach(p => {
            pledges.push(Pledge(p));
        })
            
        return(
            <div className="dash">
                <table>
                    <tbody>
                        <tr>
                            <th width="25%">
                                pledge
                            </th>
                            <th width="20%">
                                black
                            </th>
                            <th>
                                white
                            </th>
                        </tr>
                        {pledges}
                    </tbody>
                </table>
                <br></br>
                <Modal/>
            </div>
        )
    }
}